// TODO event.target„ÇíÂºïÊï∞„Å´ÊåáÂÆö„Åß„Åç„Å™„ÅÑÊñπÊ≥ï„ÇíË™øÊüª„Åô„Çã„ÄÇ‰∏ÄÊó¶ÂïèÈ°å„ÅåÂá∫„Å™„ÅÑ„Åì„Å®„ÅØÁ¢∫Ë™çÊ∏à„Åø„Å™„ÅÆ„Åßany„ÅßÂõûÈÅø„ÄÇ
import { useState, VFC } from 'react';
import { Item, Icon, Button } from 'semantic-ui-react';
import { Container, Draggable, DropResult } from 'react-smooth-dnd';
import arrayMove from 'array-move';
import ResetButton from '../atoms/ResetButton';
import AddButton from '../atoms/AddButton';
import InputForm from '../atoms/InputForm';
import Introduction from '../atoms/Intoduction';

// „Çø„Çπ„ÇØ„ÅÆÂûãÂÆöÁæ©
export type Task = {
  id: number;
  title: string;
  mode: string;
};

const TaskList: VFC = () => {
  // ------------------------------------
  // state„ÅÆÂà∂Âæ°

  // „Çø„Çπ„ÇØÂêç
  const [taskName, setTaskName] = useState<string>('');
  // „Çø„Çπ„ÇØ„ÅÆid
  const [idCnt, setIdCnt] = useState(1);
  // ËøΩÂä†„Éú„Çø„É≥„ÅÆÈùûÊ¥ªÊÄß
  const [disabled, setDisabled] = useState(true);
  // „Çø„Çπ„ÇØ„ÅÆ„É™„Çπ„Éà
  const [tasks, setTask] = useState<Task[]>([
    {
      id: idCnt,
      title: 'You can Add tasks, Delete, and Sort.',
      mode: 'display',
    },
    {
      id: 999999998,
      title: 'Happy Tasking!',
      mode: 'display',
    },
    {
      id: 999999999,
      title: 'üëå',
      mode: 'display',
    },
  ]);

  // Á∑®ÈõÜÁî®„Çø„Çπ„ÇØ„Ç™„Éñ„Ç∏„Çß„ÇØ„Éà
  const [editTask, setEditTask] = useState<Task>({
    id: 0,
    title: '',
    mode: '',
  });

  // ------------------------------------
  // state„ÅÆÊìç‰ΩúÁî®Èñ¢Êï∞„ÅÆÂÆöÁæ©

  // ---„Çø„Çπ„ÇØ„ÅÆËøΩÂä†
  const addTask = () => {
    const newIdCnt = idCnt + 1;
    setIdCnt(newIdCnt);
    setTask([
      ...tasks,
      {
        id: newIdCnt,
        title: taskName,
        mode: 'display',
      },
    ]);

    // ÂÖ•Âäõ„Åï„Çå„ÅüÂÄ§„Çí„ÇØ„É™„Ç¢„Éº„Åô„Çã
    setTaskName('');
    setDisabled(true);
  };

  // ---„Çø„Çπ„ÇØ„ÅÆÂâäÈô§
  const deleteTask = (index: number) => {
    const newTasks = [...tasks];
    newTasks.splice(index, 1);
    setTask(newTasks);
  };

  // ---ÂÖ®„Å¶„ÅÆ„Çø„Çπ„ÇØ„ÅÆ„É™„Çª„ÉÉ„Éà
  const reset = () => {
    const confirmAnswer = window.confirm('Do you wanna delete All Task ?');
    // „Ç≠„É£„É≥„Çª„É´„Åï„Çå„ÅüÂ†¥Âêà„ÅØ„É™„Çª„ÉÉ„Éà„Çí‰∏≠Êñ≠
    if (!confirmAnswer) {
      return;
    }

    setTask([]);
  };

  // ---„Çø„Çπ„ÇØ„ÅÆ„Çø„Ç§„Éà„É´„ÅÆÂÖ•Âäõ„Çí„Éï„Ç©„Éº„É†„ÅßÂèó„Åë‰ªò„Åë„Çã„Åü„ÇÅ„ÅÆÈñ¢Êï∞
  const handleValue = (target: HTMLInputElement) => {
    // 35ÊñáÂ≠ó‰ª•‰∏ä„ÅÆÂÖ•Âäõ„ÅØ„Çø„Çπ„ÇØ„Åã„Çâ„ÅØ„ÅøÂá∫„Å¶„Åó„Åæ„ÅÜ„ÅÆ„ÅßÂèó„Åë‰ªò„Åë„Å™„ÅÑ
    if (target.value.length > 35) {
      return;
    }
    setTaskName(target !== null ? target.value : '');
    setDisabled(!(target.value.length > 0));
  };

  // ---„Çø„Çπ„ÇØ„Çí„Éâ„É©„ÉÉ„Ç∞&„Éâ„É≠„ÉÉ„Éó„Åß„ÇΩ„Éº„ÉàÂèØËÉΩ„Å´„Åô„Çã„Åü„ÇÅ„ÅÆÈñ¢Êï∞
  const onDrop = (dropResult: DropResult) => {
    // `DropResult` „ÅßÂûãÂÆöÁæ©
    const { removedIndex, addedIndex } = dropResult;
    setTask((tasksArray) =>
      arrayMove(tasksArray, removedIndex || 0, addedIndex || 0),
    );
  };

  const toggleInvalidControlTarget = () => {
    const targetList = document.getElementsByClassName('controlItem');
    const tempTargetList = Array.prototype.slice.call(targetList);

    // eslint-disable-next-line @typescript-eslint/no-unsafe-return
    tempTargetList.forEach((target) => target.classList.toggle('eventInvalid'));
  };

  const editTaskTitle = (task: Task) => {
    if (tasks.filter((t) => t.mode === 'edit').length > 0) {
      return;
    }
    const tempTask = task;
    tempTask.mode = 'edit';

    // „Çø„Çπ„ÇØ„ÅÆ„É™„Çπ„Éà„ÇíÊõ¥Êñ∞„Åó„Å¶ÂÜç„É¨„É≥„ÉÄ„É™„É≥„Ç∞„Åï„Åõ„Çã
    setTask([...tasks]);
    // Á∑®ÈõÜÁî®„Çø„Çπ„ÇØ„Å´ÂºïÊï∞„ÅÆ„Çø„Çπ„ÇØ„Çí„Çª„ÉÉ„Éà„Åó„Å¶Á∑®ÈõÜÂèØËÉΩ„Å´„Åô„Çã
    setEditTask(task);
    toggleInvalidControlTarget();
  };

  const handleTaskTitle = (target: HTMLInputElement) => {
    // „Çø„Çπ„ÇØ„ÅÆ„Çø„Ç§„Éà„É´„ÇíÁ∑®ÈõÜ„Åô„Çã
    const newEditTask = { ...editTask };
    newEditTask.title = target.value;
    setEditTask(newEditTask);
  };

  const onEnterForCompleteEdit = (key: string) => {
    if (key === 'Enter') {
      // „Çø„Çπ„ÇØ„É™„Çπ„Éà„ÅÆÈÖçÂàó„ÇíÊúÄÊñ∞Âåñ„Åô„Çã
      const foundEditTask = tasks.find((task: Task) => task.mode === 'edit');
      if (foundEditTask === undefined) {
        return;
      }
      const newTasks = [...tasks];
      editTask.mode = 'display';
      newTasks[newTasks.indexOf(foundEditTask)] = editTask;
      setTask(newTasks);
      toggleInvalidControlTarget();
    }
  };

  // useEffect(() => {
  //   document.addEventListener('mousedown', (e) => {
  //     if (e.target === null) {
  //       return;
  //     }
  //     const targetElement = e.target as HTMLElement;
  //     if (
  //       !targetElement.closest('.editInputForm') &&
  //       tasks.filter((task: Task) => task.mode === 'edit').length > 0
  //     ) {
  //       const answer = window.confirm('please keydown Enter');
  //       if (answer) {
  //         onEnterForCompleteEdit('Enter');
  //       } else {
  //         // „Çø„Çπ„ÇØ„ÅÆ„É¢„Éº„Éâ„ÇíÂÖÉ„Å´Êàª„Åô
  //       }
  //     }
  //   });
  //   // eslint-disable-next-line react-hooks/exhaustive-deps
  // }, [editTask]);

  return (
    <>
      <div className="task-list-container">
        <InputForm
          taskName={taskName}
          onChangeHandler={(e) => handleValue(e.target)}
        />
        <AddButton addTask={addTask} disabled={disabled} />
        <ResetButton reset={reset} />

        <Item.Group>
          <Container
            dragHandleSelector=".dragHandleSelector"
            lockAxis="y"
            onDrop={onDrop}
          >
            {tasks.map((task, index) => (
              <Draggable key={task.id}>
                <Item key={task.id} className="task">
                  <Item.Content>
                    <Icon className="taskIcon" name="certificate" size="big" />
                    <span className="taskNumber">{index + 1}</span>
                    {task.mode === 'edit' ? (
                      <input
                        className="editInputForm"
                        type="text"
                        value={editTask.title}
                        onChange={(e) => handleTaskTitle(e.target)}
                        onKeyPress={(e) => onEnterForCompleteEdit(e.key)}
                      />
                    ) : (
                      <Item.Header
                        className="taskHeader"
                        onDoubleClick={() => editTaskTitle(task)}
                      >
                        {task.title}
                      </Item.Header>
                    )}
                    <Icon
                      className="dragHandleSelector controlItem"
                      name="sort"
                      size="big"
                    />
                    <Button
                      className="delete controlItem"
                      color="yellow"
                      size="mini"
                      onClick={() => deleteTask(index)}
                    >
                      √ó
                    </Button>
                  </Item.Content>
                </Item>
              </Draggable>
            ))}
          </Container>
        </Item.Group>
        <Introduction />
      </div>
    </>
  );
};

export default TaskList;
